@import '../../../variables.scss';

.modal {
  &-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(3px);
    z-index: 50;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.step {
  &-container {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
    box-sizing: border-box;
    max-width: 600px;
  }
  &-buttons {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    margin: -15px;
    &-btn {
      @include blur-background-hover-active;
      outline: none;
      border: none;
      color: #fff;
      cursor: pointer;
      width: 240px;
      height: 150px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: $standart-border-radius;
      text-transform: lowercase;
      font-size: 25px;
      font-weight: 500;
      margin: 15px;
      box-sizing: border-box;
      overflow: hidden;
      @media screen and (max-width: 550px) {
        height: 80px;
        width: 200px;
      }
    }
  }
}