@import '../../../variables.scss';

.product {
  width: 280px;
  height: 495px;
  border-radius: $standart-border-radius;
  @include blur-background-hover-active;
  margin: 10px;
  cursor: pointer;
  overflow: hidden;
  perspective: 1000px;
  &:hover {
    .product-content {
      transform: rotateY(180deg);
    }
  }
  &-content {
    position: relative;
    width: 100%;
    height: 100%;
    transition: transform 0.8s;
    transform-style: preserve-3d;
    text-align: center;
    &-front, &-back {
      position: absolute;
      width: 100%;
      height: 100%;
      -webkit-backface-visibility: hidden;
      backface-visibility: hidden;
    }
    &-front {
      img {
        width: 100%;
        height: 100%;
        border-radius: $standart-border-radius;
      }
    }
    &-back {
      transform: rotateY(180deg);
      padding: 20px;
      box-sizing: border-box;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      font-size: 18px;
      overflow-y: auto;
      h3 {
        margin-top: 0;
      }
      &-styles {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin: -5px;
        padding-top: 5px;
        &-style {
          margin: 5px;
          padding: 3px 10px;
          background: #ffffff;
          color: #000;
          font-size: 18px;
          font-weight: 500;
          border-radius: 4px;
          user-select: none;
          text-transform: capitalize;
        }
      }
    }
  }
}