@import '../../../variables.scss';

.catalog {
  width: 100%;
  min-height: 100vh;
  background: $page-bg-gradient;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  font-family: 'Gilroy';
  padding: 60px 0;
  &-wrapper {
    padding-top: 30px;
    display: grid;
    grid-template-columns: 280px 1fr;
    column-gap: 20px;
    align-items: flex-start;
    @media screen and (max-width: 800px) {
      grid-template-columns: 1fr;
    }
  }
  .content-column {
    grid-template-columns: 1fr;
  }
  &-filters {
    display: flex;
    flex-direction: column;
    user-select: none;
    color: #fff;
    &-header {
      display: flex;
      justify-content: space-between;
      box-sizing: border-box;
      padding: 15px 20px;
      border-radius: $standart-border-radius;
      cursor: pointer;
      transition: all .2s ease;
      @include blur-background-hover-active;
    }
    &-filter {
      margin-top: 10px;
      display: flex;
      flex-direction: column;
      padding: 0 20px;
      border-radius: $standart-border-radius;
      @include blur-background-hover;
      &-block {
        .bp3-control {
          label {
            outline: none !important;
          }
          .bp3-control-indicator {
            background: #fff;
            outline: none !important;
          }
          input {
            &:checked {
              ~.bp3-control-indicator {
                background: #0085FF;
                &::before {
                  background-image: none;
                }
              }
            }
          }
        }
        &:last-child {
          padding-bottom: 20px;
        }
        padding-top: 20px;
        .bp3-slider {
          width: 90%;
          margin: 0 auto;
          .bp3-start, .bp3-end {
            span {
              background: #1e2e6d;
            }
          }
        }
      }
    }
  }
  &-products {
    display: flex;
    flex-wrap: wrap;
    margin: -10px;
    @media screen and (max-width: 800px) {
      justify-content: center;
      padding-top: 20px;
    }
    h3, p {
      color: #fff;
    }
  }
}