@import '../../../variables.scss';


.header {
  font-family: 'Gilroy';
  position: fixed;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 50px;
  color: #fff;
  user-select: none;
  z-index: 48;
  @include blur-background-hover;
  &-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 50px;
  }
  .logo {
    transition: all .2s ease;
    img {
      height: 32px;
      margin-top: 6px;
      @media screen and (max-width: 500px) {
        height: 20px;
      }
    }
  }
  nav {
      margin-top: -6px;
      display: flex;
      align-items: center;
      text-transform: lowercase;
      font-weight: normal;
      font-size: 19px;
      @media screen and (max-width: 500px) {
        display: none;
      }
      a {
        padding: 3px 0;
        text-decoration: none;
        color: #fff;
        letter-spacing: 3px;
        transition: all .2s ease;
        display: flex;
        opacity: 1;
        align-items: center;
        &:hover {
          opacity: .8;
        }
        &:not(:last-child) {
          margin-right: 10px;
        }
      }
      .active-link {
        transform: translateY(-2px);
        pointer-events: none;
      }
      .cabinet-link {
          .user {
            display: flex;
            align-items: center;
            transition: all .2s ease;
            img {
              width: 40px;
              height: 40px;
              border-radius: 50%;
              margin-left: 5px;
            }
          }
          &:hover {
            .user {
              opacity: .7;
            }
          }
      }
      .splitter {
        margin-right: 10px;
      }
    .sign-btn {
      color: #fff;
      text-transform: lowercase;
      font-weight: normal;
      font-size: 19px;
      transition: all .2s ease;
      letter-spacing: 3px;
      display: flex;
      align-items: center;
      opacity: 1;
      cursor: pointer;
      &:hover {
        opacity: .7;
      }
    }
  }
  .burger {
    transform: rotate(90deg);
    font-size: 20px;
    display: none;
    @media screen and (max-width: 500px) {
      display: block;
    }
  }
  .mobile-menu {
    box-sizing: border-box;
    position: fixed;
    width: 50%;
    height: 100vh;
    top: 50px;
    right: 0;
    background: rgba(0, 0, 0, 0.9);
    backdrop-filter: blur(20px);
    display: flex;
    flex-direction: column;
    padding: 20px 10px;
    padding-left: 30px;
    display: none;
    transform: translateX(100%);
    transition: all .5s ease;
    font-size: 17px;
    @media screen and (max-width: 500px) {
      display: flex;
    }
    a {
      color: #fff;
      text-decoration: none;
      &:not(:first-child) {
        margin-top: 8px;
      }
    }
    .router-link-exact-active {
      color: #ffbb00;
      pointer-events: none;
      &:hover {
        color: #ffbb00;
      }
    }
    .sign-btn {
      margin-top: 8px;
    }
  }
  .opened {
    transform: translateX(0);
  }
}