@import '../../../variables.scss';

.home {
  height: 100vh;
  width: 100%;
  min-height: 550px;
  max-height: 100vh;
  background: $page-bg-gradient;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  font-family: 'Gilroy';
  .container {
    height: 100%;
  }
  &-center {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    &-logo {
      width: 600px;
      @media screen and (max-width: 500px) {
        margin-top: -60px;
      }
      img {
        width: 100%;
      }
      @media screen and (max-width: 670px) {
        width: 100%;
      }
    }
    &-btn {
      button {
        outline: none;
        border: none;
        color: #fff;
        width: 230px;
        height: 60px;
        cursor: pointer;
        font-size: 45px;
        border-radius: 40px;
        margin-top: 15px;
        @include blur-background-hover-active;
        @media screen and (max-width: 500px) {
          margin-top: 30px;
        }
      }
    }
    &-description {
      position: absolute;
      max-width: 450px;
      min-width: 280px;
      text-align: center;
      color: #fff;
      font-weight: 500;
      font-size: 20px;
      bottom: 40px;
      @media screen and (max-width: 500px) {
        bottom: 85px;
      }
    }
  }
  .modal-content {
    h3 {
      letter-spacing: 3px;
    }
  }
}